import { useRouter } from "next/router";
import { useTranslation } from "@fanfanlo";

import HomeIcon from '@mui/icons-material/Home';
import SettingsIcon from '@mui/icons-material/Settings';
import CreateIcon from '@mui/icons-material/Create';
import { BottomNavigation, BottomNavigationAction } from "@mui/material";
interface TabInfoType {
  label: string;
  value: string;
  icon: React.ReactNode;
}

export function NavBar() {
  const router = useRouter();
  const { pathname } = router;
  const { t } = useTranslation('homepage/components/navbar/content');
  const tabInfoList: TabInfoType[] = [
    { label: t('NavBar.home'), value: '/', icon: <HomeIcon /> },
    { label: t('NavBar.create'), value: '/script-creator', icon: <CreateIcon /> },
    // { label: t('content.market'), value: '/market', icon: <StorefrontIcon /> },
    { label: t('NavBar.settings'), value: '/settings', icon: <SettingsIcon /> },
  ];
  return (

    <BottomNavigation showLabels value={pathname}>
      {tabInfoList.map(e => (
        <BottomNavigationAction
          key={e.value}
          label={e.label}
          icon={e.icon}
          data-id={`tabbar-${e.value}`}
          className={pathname === e.value ? 'Mui-selected' : ''}
          onClick={() => {
            if (pathname === e.value) return;
            router.push(e.value);
          }}
        />
      ))}
    </BottomNavigation>
  );
}